import React, {useCallback, useEffect, useState} from 'react';
import Context from './context';
import usePolyDogeFinance from '../../hooks/usePolyDogeFinance';
import {Bank} from '../../polydoge-finance';
import config, {bankDefinitions} from '../../config';

const Banks: React.FC = ({children}) => {
  const [banks, setBanks] = useState<Bank[]>([]);
  const polydogeFinance = usePolyDogeFinance();
  const isUnlocked = polydogeFinance?.isUnlocked;

  const fetchPools = useCallback(async () => {
    const banks: Bank[] = [];

    for (const bankInfo of Object.values(bankDefinitions)) {
      if (bankInfo.finished) {
        if (!polydogeFinance.isUnlocked) continue;

        // only show pools staked by user
        const balance = await polydogeFinance.stakedBalanceOnBank(
          bankInfo.contract,
          bankInfo.poolId,
          polydogeFinance.myAccount,
        );
        if (balance.lte(0)) {
          continue;
        }
      }
      banks.push({
        ...bankInfo,
        address: config.deployments[bankInfo.contract].address,
        depositToken: polydogeFinance.externalTokens[bankInfo.depositTokenName],
        earnToken: bankInfo.earnTokenName === 'PDDOLLAR' ? polydogeFinance.PDDOLLAR : polydogeFinance.PDSHARE,
      });
    }
    banks.sort((a, b) => (a.sort > b.sort ? 1 : -1));
    setBanks(banks);
  }, [polydogeFinance, setBanks]);

  useEffect(() => {
    if (polydogeFinance) {
      fetchPools().catch((err) => console.error(`Failed to fetch pools: ${err.stack}`));
    }
  }, [isUnlocked, polydogeFinance, fetchPools]);

  return <Context.Provider value={{banks}}>{children}</Context.Provider>;
};

export default Banks;
