import React from 'react';

//Graveyard ecosystem logos
import pddollarLogo from '../../assets/img/pddollar-logo2.png';
import tShareLogo from '../../assets/img/pdshares-final2.png';
import pddollarLogoPNG from '../../assets/img/pddollar-final3.png';
import tShareLogoPNG from '../../assets/img/pdshares-final2.png';
import tBondLogo from '../../assets/img/pdbond.png';

import pddollarFtmLpLogo from '../../assets/img/pddollar-matic-lp.png';
import pddollarPdogeLpLogo from '../../assets/img/pddollar-polydoge-lp.png';
import bshareFtmLpLogo from '../../assets/img/pdshares-matic-lp.png';
import bshareEthLpLogo from '../../assets/img/pdshares-eth-lp.png';

import bnbLogo from '../../assets/img/matic.png';
import btcLogo from '../../assets/img/eth-logo.png';
import usdcLogo from '../../assets/img/USDC.png';
import nachoLogo from '../../assets/img/nacho.png';
import polydogeLogo from '../../assets/img/polydogeLogo.png';
import node from '../../assets/img/node.png';
import pddollarUsdcLpLogo from '../../assets/img/pddollar-usdc-lp.png';
import pddollarpdsLpLogo from '../../assets/img/pddollar-pdshare-lp.png';

const logosBySymbol: { [title: string]: string } = {
  //Real tokens
  //=====================
  PDDOLLAR: pddollarLogo,
  PDDOLLAR1: pddollarLogo,
  PDDOLLARPNG: pddollarLogoPNG,
  PDSHAREPNG: tShareLogoPNG,
  PDSHARE: tShareLogo,
  PDSHARE1: tShareLogo,
  PDBOND: tBondLogo,
  WETH: bnbLogo,
  BOO: bnbLogo,
  SHIBA: bnbLogo,
  ZOO: bnbLogo,
  CAKE: bnbLogo,
  NACHO: nachoLogo,
  POLYDOGE: polydogeLogo,
  SUSD: bnbLogo,
  SMATIC: btcLogo,
  MATIC: btcLogo,
  WMATIC: btcLogo,
  USDC: usdcLogo,
  SVL: bnbLogo,
  GNODE: node,
  'MATIC-ETH-LP': pddollarFtmLpLogo,
  'PDDOLLAR-WMATIC-LP': pddollarFtmLpLogo,
  'PDDOLLAR-USDC-LP': pddollarUsdcLpLogo,
  'PDSHARE-PDDOLLAR-LP': pddollarpdsLpLogo,
  'PDSHARE-ETH-LP': bshareEthLpLogo,
  'PDSHARE-WMATIC-LP': bshareFtmLpLogo,
  'PDSHARE-ETH-APELP': bshareFtmLpLogo,
  'PDDOLLAR-WETH-LP': pddollarFtmLpLogo,
  'PDDOLLAR-POLYDOGE-LP': pddollarPdogeLpLogo,
};

type LogoProps = {
  symbol: string;
  size?: number;
};

const TokenSymbol: React.FC<LogoProps> = ({symbol, size = 85}) => {
  if (!logosBySymbol[symbol]) {
    throw new Error(`Invalid Token Logo symbol: ${symbol}`);
  }
  if(symbol === 'PDDOLLAR-MIM-LP' || symbol === 'PDSHARE-WETH-LP' ){
    return <img src={logosBySymbol[symbol]} alt={`${symbol} Logo`} width={95} height={60} />;
  }else if(symbol === 'USDC' || symbol === 'WETH'|| symbol === 'MATIC' || symbol === 'NACHO' ){
    return <img src={logosBySymbol[symbol]} alt={`${symbol} Logo`} width={65} height={65} />;
  }else
  return <img src={logosBySymbol[symbol]} alt={`${symbol} Logo`} width={size} height={size} />;
};

export default TokenSymbol;
