import {useEffect, useState} from 'react';
import usePolyDogeFinance from './usePolyDogeFinance';
import {TokenStat} from '../polydoge-finance/types';
import useRefresh from './useRefresh';

const usePDDollarStats = () => {
  const [stat, setStat] = useState<TokenStat>();
  const {fastRefresh} = useRefresh();
  const polydogeFinance = usePolyDogeFinance();

  useEffect(() => {
    async function fetchPDDollarPrice() {
      try {
        setStat(await polydogeFinance.getPDDollarStat());
      } catch (err) {
        console.error(err);
      }
    }
    fetchPDDollarPrice();
  }, [setStat, polydogeFinance, fastRefresh]);

  return stat;
};

export default usePDDollarStats;
