import React, {createContext, useEffect, useState} from 'react';
import {useWallet} from 'use-wallet';
import PolyDogeFinance from '../../polydoge-finance';
import config from '../../config';

export interface PolyDogeFinanceContext {
  polydogeFinance?: PolyDogeFinance;
}

export const Context = createContext<PolyDogeFinanceContext>({polydogeFinance: null});

export const PolyDogeFinanceProvider: React.FC = ({children}) => {
  const {ethereum, account} = useWallet();
  const [polydogeFinance, setPolyDogeFinance] = useState<PolyDogeFinance>();

  useEffect(() => {
    if (!polydogeFinance) {
      const pddollar = new PolyDogeFinance(config);
      if (account) {
        // wallet was unlocked at initialization
        pddollar.unlockWallet(ethereum, account);
      }
      setPolyDogeFinance(pddollar);
    } else if (account) {
      polydogeFinance.unlockWallet(ethereum, account);
    }
  }, [account, ethereum, polydogeFinance]);

  return <Context.Provider value={{polydogeFinance}}>{children}</Context.Provider>;
};
