import React from 'react';
import {Container, Grid, Link} from '@material-ui/core';
import {ReactComponent as IconTelegram} from '../../assets/img/telegram.svg';
import {ReactComponent as IconTwitter} from '../../assets/img/twitter.svg';
import {ReactComponent as IconGithub} from '../../assets/img/github.svg';
import {ReactComponent as IconDiscord} from '../../assets/img/discord.svg';
import '../../index.css';

const Footer = () => {
  return (
    <footer>
      <Container>
        <Grid container spacing={3}>
          <Grid item xs={12} sm={6} className='colFooter_1'>
            <div className='copyright'>
              {'Copyright  '}
              <Link color="inherit" href="/">
                PolyDoge DAO
              </Link>
              {new Date().getFullYear()}
            </div>
          </Grid>
          <Grid item xs={12} sm={6} className='colFooter_2'>
            <a href="https://twitter.com/PolyDoge" rel="noopener noreferrer" target="_blank" >
              <IconTwitter style={{ fill: '#ffffff', height: '20px'}} />
            </a>
            <a href="https://github.com/PolyDogeDAO" rel="noopener noreferrer" target="_blank">
              <IconGithub style={{ fill: '#ffffff', height: '20px'}} />
            </a>
            <a href="https://t.me/polydoge" rel="noopener noreferrer" target="_blank" >
              <IconTelegram style={{ fill: '#ffffff', height: '20px'}} />
            </a>
            <a href="https://discord.com/invite/FfkHu4HEBn" rel="noopener noreferrer" target="_blank">
              <IconDiscord style={{ fill: '#ffffff', height: '20px'}} />
            </a>
          </Grid>
        </Grid>
      </Container>
    </footer>
  );
};

export default Footer;
