  async getPDDollarStat(): Promise<TokenStat> {
    const { PDDollarRewardPool, PDDollarGenesisRewardPool } = this.contracts;
    const supply = await this.PDDOLLAR.totalSupply();
    //  console.log('kio', this.PDDOLLAR, supply);

    const pddollarRewardPoolSupply = await this.PDDOLLAR.balanceOf(PDDollarGenesisRewardPool.address);
    const pddollarRewardPoolSupply2 = await this.PDDOLLAR.balanceOf(PDDollarRewardPool.address);
    const pddollarCirculatingSupply = supply.sub(pddollarRewardPoolSupply).sub(pddollarRewardPoolSupply2);
    const priceInETH = await this.getTokenPriceFromPancakeswap(this.PDDOLLAR);
    const priceInMATIC = await this.getTokenPriceFromPancakeswapMATIC(this.PDDOLLAR);
    const priceOfOneETH = await this.getWETHPriceFromPancakeswap();
    const priceOfOneMATIC = await this.getMATICPriceFromPancakeswap();
    const priceInDollars = await this.getTokenPriceFromPancakeswapPDDOLLARUSD();
    const priceOfPDDollarInDollars = ((Number(priceInMATIC) * Number(priceOfOneMATIC)) / 1).toFixed(5);
    // console.log('priceOfPDDollarInDollars', priceOfPDDollarInDollars);

    return {
      //  tokenInFtm: (Number(priceInETH) * 100).toString(),
      tokenInFtm: priceInMATIC ? priceInMATIC.toString() : '0',
      priceInDollars: priceOfPDDollarInDollars,
      totalSupply: getDisplayBalance(supply, this.PDDOLLAR.decimal, 0),
      circulatingSupply: getDisplayBalance(pddollarCirculatingSupply, this.PDDOLLAR.decimal, 0),
    };
  }

  async getMATICPriceUSD(): Promise<Number> {
    const priceOfOneMATIC = await this.getMATICPriceFromPancakeswap();
    return Number(priceOfOneMATIC);
  }

  /**
   * Calculates various stats for the requested LP
   * @param name of the LP token to load stats for
   * @returns
   */
  async getLPStat(name: string): Promise<LPStat> {
    const lpToken = this.externalTokens[name];
    const lpTokenSupplyBN = await lpToken.totalSupply();
    const lpTokenSupply = getDisplayBalance(lpTokenSupplyBN, 18);
    const token0 = name.startsWith('PDDOLLAR') ? this.PDDOLLAR : this.PDSHARE;
    const isPDDollar = name.startsWith('PDDOLLAR');
    const tokenAmountBN = await token0.balanceOf(lpToken.address);
    const tokenAmount = getDisplayBalance(tokenAmountBN, 18);

    const ftmAmountBN = await this.ETH.balanceOf(lpToken.address);
    const ftmAmount = getDisplayBalance(ftmAmountBN, 18);
    const tokenAmountInOneLP = Number(tokenAmount) / Number(lpTokenSupply);
    const ftmAmountInOneLP = Number(ftmAmount) / Number(lpTokenSupply);
    const lpTokenPrice = await this.getLPTokenPrice(lpToken, token0, isPDDollar);
    const lpTokenPriceFixed = Number(lpTokenPrice).toFixed(2).toString();
    const liquidity = (Number(lpTokenSupply) * Number(lpTokenPrice)).toFixed(2).toString();
    return {
      tokenAmount: tokenAmountInOneLP.toFixed(2).toString(),
      ftmAmount: ftmAmountInOneLP.toFixed(2).toString(),
      priceOfOne: lpTokenPriceFixed,
      totalLiquidity: liquidity,
      totalSupply: Number(lpTokenSupply).toFixed(2).toString(),
    };
  }

  async getLPStatMATIC(name: string): Promise<LPStat> {
    const lpToken = this.externalTokens[name];
    const lpTokenSupplyBN = await lpToken.totalSupply();
    const lpTokenSupply = getDisplayBalance(lpTokenSupplyBN, 18);
    const token0 = name.startsWith('PDDOLLAR') ? this.PDDOLLAR : this.PDSHARE;
    const isPDDollar = name.startsWith('PDDOLLAR');
    const tokenAmountBN = await token0.balanceOf(lpToken.address);
    const tokenAmount = getDisplayBalance(tokenAmountBN, 18);

    const btcAmountBN = await this.MATIC.balanceOf(lpToken.address);
    const btcAmount = getDisplayBalance(btcAmountBN, 18);
    const tokenAmountInOneLP = Number(tokenAmount) / Number(lpTokenSupply);
    const ftmAmountInOneLP = Number(btcAmount) / Number(lpTokenSupply);
    const lpTokenPrice = await this.getLPTokenPrice(lpToken, token0, isPDDollar);

    const lpTokenPriceFixed = Number(lpTokenPrice).toFixed(2).toString();

    const liquidity = (Number(lpTokenSupply) * Number(lpTokenPrice)).toFixed(2).toString();

    return {
      tokenAmount: tokenAmountInOneLP.toFixed(2).toString(),
      ftmAmount: ftmAmountInOneLP.toFixed(5).toString(),
      priceOfOne: lpTokenPriceFixed,
      totalLiquidity: liquidity,
      totalSupply: Number(lpTokenSupply).toFixed(2).toString(),
    };
  }
  /**
   * Use this method to get price for PDDollar
   * @returns TokenStat for PDBOND
   * priceInETH
   * priceInDollars
   * TotalSupply
   * CirculatingSupply (always equal to total supply for bonds)
   */
  async getBondStat(): Promise<TokenStat> {
    const { Treasury } = this.contracts;
    const pddollarStat = await this.getPDDollarStat();
    const bondPDDollarRatioBN = await Treasury.getBondPremiumRate();
    const modifier = bondPDDollarRatioBN / 1e18 > 1 ? bondPDDollarRatioBN / 1e18 : 1;
    const bondPriceInETH = (Number(pddollarStat.tokenInFtm) * modifier).toFixed(2);
    const priceOfPDBondInDollars = (Number(pddollarStat.priceInDollars) * modifier).toFixed(2);
    const supply = await this.PDBOND.displayedTotalSupply();
    return {
      tokenInFtm: bondPriceInETH,
      priceInDollars: priceOfPDBondInDollars,
      totalSupply: supply,
      circulatingSupply: supply,
    };
  }

  /**
   * @returns TokenStat for PDSHARE
   * priceInETH
   * priceInDollars
   * TotalSupply
   * CirculatingSupply (always equal to total supply for bonds)
   */
  async getShareStat(): Promise<TokenStat> {
    const { PDShareRewardPool } = this.contracts;

    const supply = await this.PDSHARE.totalSupply();

    const priceInETH = await this.getTokenPriceFromPancakeswap(this.PDSHARE);
    const pddollarRewardPoolSupply = await this.PDSHARE.balanceOf(PDShareRewardPool.address);
    const tShareCirculatingSupply = supply.sub(pddollarRewardPoolSupply);
    const priceOfOneETH = await this.getWETHPriceFromPancakeswap();
    const priceOfSharesInDollars = (Number(priceInETH) * Number(priceOfOneETH)).toFixed(2);

    return {
      tokenInFtm: priceInETH,
      priceInDollars: priceOfSharesInDollars,
      totalSupply: getDisplayBalance(supply, this.PDSHARE.decimal, 0),
      circulatingSupply: getDisplayBalance(tShareCirculatingSupply, this.PDSHARE.decimal, 0),
    };
  }

  async getPDDollarStatInEstimatedTWAP(): Promise<TokenStat> {
    const { Oracle, PDDollarRewardPool } = this.contracts;
    const expectedPrice = await Oracle.twap(this.PDDOLLAR.address, ethers.utils.parseEther('1'));

    const supply = await this.PDDOLLAR.totalSupply();
    const pddollarRewardPoolSupply = await this.PDDOLLAR.balanceOf(PDDollarRewardPool.address);
    const pddollarCirculatingSupply = supply.sub(pddollarRewardPoolSupply);
    return {
      tokenInFtm: getDisplayBalance(expectedPrice),
      priceInDollars: getDisplayBalance(expectedPrice),
      totalSupply: getDisplayBalance(supply, this.PDDOLLAR.decimal, 0),
      circulatingSupply: getDisplayBalance(pddollarCirculatingSupply, this.PDDOLLAR.decimal, 0),
    };
  }

  async getPDDollarPriceInLastTWAP(): Promise<BigNumber> {
    const { Treasury } = this.contracts;
    return Treasury.getPDDollarUpdatedPrice();
  }

